import React from 'react';
import Chess from './chess';
import { EMPTY } from '../constant/chess';
class Board extends React.Component {
  constructor(props) {
    const piece = [
      5, 3, 2, 1, 6, 1, 2, 3, 5,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      7, 4, 7, 7, 7, 7, 7, 4, 7,
      0, 7, 0, 7, 0, 7, 0, 7, 0,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      0, 7, 0, 7, 0, 7, 0, 7, 0,
      7, 4, 7, 7, 7, 7, 7, 4, 7,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      5, 3, 2, 1, 6, 1, 2, 3, 5
    ];
    const colors = [
      0, 0, 0, 0, 0, 0, 0, 0, 0,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      7, 0, 7, 7, 7, 7, 7, 0, 7,
      0, 7, 0, 7, 0, 7, 0, 7, 0,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      1, 7, 1, 7, 1, 7, 1, 7, 1,
      7, 1, 7, 7, 7, 7, 7, 1, 7,
      7, 7, 7, 7, 7, 7, 7, 7, 7,
      1, 1, 1, 1, 1, 1, 1, 1, 1
    ];
    super(props);
    this.onChessClickHandle = this.onChessClickHandle.bind(this);
    this.state = {
      piece: piece,
      color: colors,
      clickedIndex: null,
    };
  }
  onChessClickHandle(index, e) {
    const piece = this.state.piece;
    const color = this.state.color;
    var clickedIndex = this.state.clickedIndex;
    if (clickedIndex == null) {
      this.setState({
        clickedIndex: index,
      })
      return;
    }
    piece[index] = this.state.piece[clickedIndex];
    piece[clickedIndex] = EMPTY;
    color[index] = this.state.color[clickedIndex];
    color[clickedIndex] = EMPTY;
    this.setState({
      piece: piece,
      color: color,
      clickedIndex: null,
    })
  }
  render() {
    const piece = this.state.piece;
    const color = this.state.color;
    const listChess = piece.map((val, key) =>
      <Chess key={key} id={key} val={val} color={color[key]} onChessClickHandle={this.onChessClickHandle}></Chess>
    );
    return (
      <div className="board">
        <img alt="" src="images/bg.gif" />
        {listChess}
      </div>
    );
  }
}
export default Board;